* {
  margin: 0;
  padding: 0;
}

.body {
  width: 100%;
  height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, .6), rgba(0, 0, 0, .8)), url(images/bg.jpg) no-repeat;
  background-size: cover;
}

.viewport {
  height: 21vw;
  width: 42vw;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  perspective: 1300px;
}

.viewport::after {
  width: 120%;
  height: 20%;
  top: 140%;
  left: -10%;
  content: '';
  position: absolute;
  background-color: #000;
  z-index: -1;
  filter: blur(50px);
}

.cube {
  width: 100%;
  height: 33.3333333%;
  transform-style: preserve-3d;
  position: relative;
}

.cube-1 {
  transition: transform .4s;
}

.cube-2 {
  z-index: 10;
  transition: transform .4s .2s;
}

.cube-3 {
  transition: transform .4s .4s;
}

.face {
  width: 100%;
  height: 100%;
  position: absolute;
}

.cube-1 .front {
  background: no-repeat 50% 0;
  background-size: cover;
}

.cube-2 .front {
  background: no-repeat 50% -7vw;
  background-size: cover;
}

.cube-3 .front {
  background: no-repeat 50% -14vw;
  background-size: cover;
}

.cube-1 .right {
  background: no-repeat 50% 0;
  background-size: cover;
}

.cube-2 .right {
  background: no-repeat 50% -7vw;
  background-size: cover;
}

.cube-3 .right {
  background: no-repeat 50% -14vw;
  background-size: cover;
}

.cube-1 .back {
  background: no-repeat 50% 0;
  background-size: cover;
}

.cube-2 .back {
  background: no-repeat 50% -7vw;
  background-size: cover;
}

.cube-3 .back {
  background: no-repeat 50% -14vw;
  background-size: cover;
}

.cube-1 .left {
  background: no-repeat 50% 0;
  background-size: cover;
}

.cube-2 .left {
  background: no-repeat 50% -7vw;
  background-size: cover;
}

.cube-3 .left {
  background: no-repeat 50% -14vw;
  background-size: cover;
}

.front {
  transform: translateZ(21vw);
}

.right {
  transform: translateX(21vw) rotateY(90deg);
}

.back {
  transform: translateZ(-21vw) rotateY(180deg);
}

.left {
  transform: translateX(-21vw) rotateY(-90deg);
}

.top {
  height: 42vw;
  background-color: #111;
  transform: translateY(-21vw) rotateX(90deg);
}

.bottom {
  height: 42vw;
  background-color: #111;
  transform: translateY(-14vw) rotateX(-90deg);
}

.control {
  width: 40px;
  height: 40px;
  align-items: center;
  color: #fff;
  position: absolute;
  border-radius: 100%;
  transform: translate(-50%, -50%);
  border: 1px solid #fff;
  background-color: rgba(59, 52, 50, .7);
  display: flex;
  justify-content: center;
  cursor: pointer;
  z-index: 100;
  transition: background-color .3s;
}

.control:hover {
  background-color: rgba(42, 38, 36, .8);
}

.control i {
  pointer-events: none;
}

.left-arrow {
  top: 50%;
  left: -35%;
}

.right-arrow {
  top: 50%;
  left: 135%;
}

.play-pause {
  top: 140%;
  left: 120%;
}

.twitter{
  color: white;
}

.twitter:hover{
  color: #1DA1F2;
}

.custom-btn {
  width: 150px;
  height: 40px;
  padding: 10px 25px;
  border: 2px solid #000;
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
}

.btn-1 {
  background: transparent;
  color: #fff;
  line-height: 42px;
  padding: 0;
  border: none;
}
.btn-1 span {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.btn-1:before,
.btn-1:after {
  position: absolute;
  content: "";
  height: 0;
  width: 2px;
  background: transparent;
}
.btn-1:before {
  right: 0;
  top: 0;
  transition: all 500ms ease;
}
.btn-1:after {
  left: 0;
  bottom: 0;
  transition: all 500ms ease;
}
.btn-1:hover{
  color: #aaa;
}
.btn-1:hover:before {
  transition: all 500ms ease;
  height: 100%;
}
.btn-1:hover:after {
  transition: all 500ms ease;
  height: 100%;
}
.btn-1 span:before,
.btn-1 span:after {
  position: absolute;
  content: "";
  background: transparent;
}
.btn-1 span:before {
  left: 0;
  top: 0;
  width: 0;
  height: 2px;
  transition: all 500ms ease;
}
.btn-1 span:after {
  right: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  transition: all 500ms ease;
}
.btn-1 span:hover:before {
  width: 100%;
}
.btn-1 span:hover:after {
  width: 100%;
}

#banner{
  background: url("https://firebasestorage.googleapis.com/v0/b/chjvegltyq.appspot.com/o/canvas-of-consciousness%2F1630595025.jpg?alt=media") center;
  -webkit-background-clip: text;
  color: transparent;
  font-weight: 1000;
}
